</template>


<script>
export default {
  name: "NavBar",
  data: function () {
    return {
      isScroll: false,
    };
  },
  computed: {
    classObject: function () {
      window.addEventListener("scroll", () => {
        if (window.scrollY > 20) {
            this.isScroll = true
        } else {
            this.isScroll = false
        }
      });
      return {
        'scroll': this.isScroll,
        'Noscroll': !this.isScroll,
      };
    },
  },
};
</script>

